@import url('https://fonts.googleapis.com/css?family=VT323');


.service-con{
    max-width: 88rem;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    margin: auto;  
    display: none;
}

.ser-main{
    padding-left: 15px;
    padding-right: 15px;
  }

  .service-div{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .service-intro{
    cursor: pointer;
    margin-top: 5rem;
  }

  .service-intro p{
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    color: royalblue;
    font-style: oblique;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.service-intro h2{
    text-align: center;
    font-size: 33px;
    font-family: 'fangsong', 'math';
    font-weight: bolder;
    padding: 7px;
    line-height: 1.2;
    color: #333;
}

.service-intro dd{
    color: #000000db;
    font-size: 14px;
    font-weight: 400;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-align: center;
}
  

.service-container  {
  display: flex;
  justify-content: space-between;
  align-items: stretch; /* Stretch items vertically to have equal height */
  margin-top: 40px;
  flex-direction: column;
  row-gap: 30px;
}

.first-set{
  display: flex;
}

.second-set{
  display: flex;
}