@media screen and (max-width: 1011px) {
   .hire{
    display: none;
   }

   
}

@media screen and (max-width: 1004px) {
    nav{
     display: none;
    }

    .dropdown{
        display: inline-block;
    }
    .hire{
        display: inline-block;
       }


 }

 @media screen and (max-width: 1000px) {
    nav{
     display: none;
    }

    .dropdown{
        display: inline-block;
    }

    .hire{
        display: none;
       }


       header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 6px;
        padding-bottom: 6px;
        padding-left: 7px;
      
       }

    
       section{
        display:flex;
        flex-direction: row;
        column-gap: 20px;
         margin-top: 18px;
        
    }

 }

 @media screen and  (max-width: 825px){
    .h1{
        font-size: 26px;
      }

      .service-intro h2{
        font-size: 24px;
      }

      section{
        display:flex;
        flex-direction: column;
        margin-top: 38px;
        row-gap: 30px;
    }

 }



 @media screen and (max-width: 700px){
    
    section{
        display:flex;
        flex-direction: column;
        margin-top: 18px;

    }

    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 8px;
        padding-right: 8px;
    
      }

      .h1{
        font-size: 23px;
      }
      
      .service-intro h2{
        font-size: 23px;
      }
 }



 @media screen and (max-width: 350px){
    

    section{
        display:flex;
        flex-direction: column;
    }

    
 }