@import url('https://fonts.googleapis.com/css?family=Marck+Script');
@import url('https://fonts.googleapis.com/css?family=Helvetica');

.header-main{
  background-color: rgb(245 241 241 / 70%);
    max-width: 88rem;
    width: 100%;
    margin: auto;
}

.section-con{
  padding-left: 15px;
  padding-right: 15px;
}

.error{
  display: none;
}

.header-main.darkmode{
  background-color: #38363661;}

.component-container{
    max-width: 88rem;
    width: 100%;
   margin: auto;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding-bottom: 6px;
  padding-left: 16px;
  padding-right: 17px;
  width: 100%;
  height: 65px;
  position: fixed;
  z-index: 10;
  top: 0;
  padding-top: 6px;
}

header.darkmode{
    background-color: black;
}
  
  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .logo img{
    height: 46px;
    width: 29px;
    object-fit: cover;
    border-radius: 74px;
    cursor: pointer; 
  margin: 0; }
  
   .logo h1{
    font-size: 30px;
    font-weight: bolder;
    color: royalblue;
     font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
     margin-bottom: -12px;
    margin-left: -3px;
  }
  
  .icon{
    font-family: 'tangerine', cursive;
    font-weight: bold;
    font-size: 31px;
    color: teal;
  }

  .hire li{
  list-style: none;
  }
  
  .hire a{
    background: purple;
    border: none;
    color: #fff;
    padding: 9px 22px;
    text-align: center;
    text-decoration: none;
    font-size: 14px;
    font-style: oblique;
    border-radius: 5px;
    cursor: pointer;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  @keyframes oscillate {
    0% { transform: translateX(0); }
    50% { transform: translateX(10px); }
    100% { transform: translateX(0); }
  }
  
  .forward {
    display: inline-block;
    animation: oscillate 1.5s infinite;
    font-size: 15px;
  }
  
  
  nav ul {
    list-style: none;
    display: flex;
  }
  
  nav ul li {
    margin-right: 23px;
  }
  
  nav ul li a {
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    color: rgb(0, 5, 5);
    font-family: 'Arial, sans-serif';
  }

  nav ul li a.darkmode {
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    color: snow;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  
  nav ul li a:hover {
text-decoration: underline;  }

  /*/Home section/*/

  .oladax-pic {
    width: 100%;
    max-width: 450px;
    height: auto;
    overflow: hidden;
    position: relative;
  }
  
  .oladax-pic {
    border-left-width: 1px;
    border-left-style: solid;
    border-bottom-style: solid;
    margin-top: 3.4rem;
    border-bottom-width: 1px;
    border-radius: 20px;
    border-color: royalblue;
    box-shadow: 2px 2px 0px blue;
    animation: shadowAnimation 5s linear infinite;
  }
  
  @keyframes shadowAnimation {
    0% {
      box-shadow: 2px 2px 0px red;
    }
    25% {
      box-shadow: 2px 2px 0px yellow;
    }
    50% {
      box-shadow: 2px 2px 0px green;
    }
    75% {
      box-shadow: 2px 2px 0px cyan;
    }
    100% {
      box-shadow: 2px 2px 0px blue;
    }
  }
  
  
  
  

  .oladax-pic img{
    margin-top: 7vh;
    width: 100%;
    object-fit: cover;
    object-position: center;  }

  .empty {
    background-color: #0056ff6e;
    width: 290px;
    border-style: groove;
    height: 163px;
    border-bottom-right-radius: 158px;
    border-top-left-radius: 130px;
    border: none;
    position: absolute;
    bottom: -42px;
    left: -223px;
  }
  
  .social-media-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    left: 9px;
    top: 50px;
  }

  .social{
    display: flex;
    justify-content:center;
    align-items: center;
    flex-direction: row;
    margin-top: -9px;
  }
  
  .social-media-icons a{
   
    color: #333;
    text-align: center;
    font-size: 18px;
    margin-top: 7px;
    border: none;
    cursor: pointer;
  }

  .social-media-icons a.darkmode{
    color:#ffffffdb;
  }
  
  
  /*/ section styling /*/
  section{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-bottom: 2rem;
    padding-top: 7rem;
    
  }

  .intro-con{
    display: flex;
    justify-content: center;;
    flex-direction: column;
    }

  .intro-con p{
    color: #000000db;
    font-size: 14px;
    font-weight: 400;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .intro-con p.darkmode{
    color: #ffffffdb;
  }


  .h1{
 white-space: pre;
 font-size: 33px;
font-family: 'fangsong', 'math';
font-weight: bolder;
margin: 10px -10px;
padding: 10px;
line-height: 1.2;
color: #333;

  }

  .h1.darkmode{
    color: rgb(204, 204, 204);

  }



  .cv-con{
    display: flex;
    margin-top: 20px;
  }

  .cv-con button{
    background: purple;
    border: none;
    color: #fff;
    text-align: center;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
    height: 38px;
    width: 130px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }
  .cv-con .play{
    color: #000000db;
    font-size: 14px;
    font-weight: 400;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background: none;
    cursor: pointer;
  }

  .cv-con .play span{
    border: solid;
    border-width: 1px;
    padding: 2px 6px;
    border-radius: 5px;
    font-size: 11px;
    margin-right: 4px;
    animation: blink 3s infinite linear; 
    color: purple;
    text-align: center;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .cv-con .play.darkmode{
    color: #ffffffdb;
  }

  @keyframes blink{
    0% {
      box-shadow: none;

    }
    50% {
    box-shadow: 0 0 0px purple, 0 0 7px gray;
      
    }
    100% {
      box-shadow: none;
    }
  }

  /*/decoration mode /*/

  .sprinkle-container{
    position: relative;
    text-align: center;
  }

  .entity {
    /* Shared styles for all entities */
    border-radius: 50%;
    color: teal;
    object-fit: cover;
    font-size: 24px;
    text-align: center;
    font-size: 20px;
    opacity: 0.2;
  }
  
  .entity1 {
    /* Styles for the first entity */
    position: absolute;
    top: 100px;
    color: gray;
    transform: rotateY(0deg);
  }
  
  .entity2 {
    /* Styles for the second entity */
    position: absolute;
    left: 20px;
    color: gray;
  }

  .entity3{
    /* Styles for the second entity */
    position: absolute;
    top: -100px;
    color: gray;
    transform: rotateY(0deg);
  }

  .entity4{
    /* Styles for the second entity */
    position: absolute;
    color: gray;
    left: 20px;
    top: -38px;
  }

  .entity5{
    /* Styles for the second entity */
    position: absolute;
    color: gray;
    top: -468px;
    right: -75px;
  }

  .entity6{
    /* Styles for the second entity */
    position: absolute;
    color: gray;
    right: -120px;
    top: -40px;
   
  }
  /* styles for dark mode*/

  .toggle{
    float: left;
    margin-top: 80px;
    position: fixed;
    margin-left: 8px;
    z-index: 3;
     }

    .toggle button{
      width: 32px;                        
      height: 27px;
     background: purple;
      color: white;
      text-align: center;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      position: absolute;
      top: 4px;
      left: 7px;
    }
  
    /*dropdown btn styling*/

    .dropdown{
      display: none;
    }

    .dropdown button{
      width: 32px;
      height: 27px;
      text-align: center;
      background: purple;
      color: white;
      border: none;
      border-radius: 5px;   
      cursor: pointer;
    }

  
  